import jwtDecode from 'jwt-decode'
import React, { useEffect, useState } from 'react'
import { createHashRouter, Navigate, RouterProvider } from 'react-router-dom'
import All from './Components/All/All'
import Action from './Components/Categories/Action'
import ActionRpg from './Components/Categories/ActionRpg'
import BattleRoyale from './Components/Categories/BattleRoyale'
import Fantasy from './Components/Categories/Fantasy'
import Flight from './Components/Categories/Flight'
import OpenWorld from './Components/Categories/OpenWorld'
import Racing from './Components/Categories/Racing'
import Shooter from './Components/Categories/Shooter'
import Social from './Components/Categories/Social'
import Sports from './Components/Categories/Sports'
import Zombie from './Components/Categories/Zombie'
import Details from './Components/Details/Details'
import Home from './Components/Home/Home'
import Login from './Components/Login/Login'
import Masterlayout from './Components/Masterlayout/Masterlayout'
import Browser from './Components/Platforms/Browser'
import Pc from './Components/Platforms/Pc'
import ProtectedRoute from './Components/ProtectedRoute/ProtectedRoute'
import Register from './Components/regiester/Register'
import Alphabetical from './Components/Sort-by/Alphabetical'
import Popularity from './Components/Sort-by/Popularity'
import ReleaseDate from './Components/Sort-by/Releasedate'
import Relevance from './Components/Sort-by/Relevance'



export default function App() {
  const [userData, setuserData] = useState(null)

  useEffect(() => {
    if(localStorage.getItem('token'))
    {
      decodedData();
    }
  }, []);
  
  let logOut =()=>{
    localStorage.removeItem('token');
    setuserData(null);
    <Navigate to='/login'/>
  }
  let decodedData=()=> 
  {
    let data = localStorage.getItem('token');
    let decoded= jwtDecode(data);
    setuserData(decoded);
  }

  let roots = createHashRouter([{path:'/',element:<Masterlayout data={userData}  logOut={logOut}/>,children:[
    {index:true,element: <ProtectedRoute data={userData}><Home/></ProtectedRoute>  },
    {path:'all',element: <ProtectedRoute data={userData}><All/></ProtectedRoute>},
    {path:'pc',element:<ProtectedRoute data={userData}><Pc/></ProtectedRoute>},
    {path:'browser',element:<ProtectedRoute data={userData}><Browser/></ProtectedRoute>},
    {path:'release-date',element:<ProtectedRoute data={userData}><ReleaseDate/></ProtectedRoute>},
    {path:'popularity',element:<ProtectedRoute data={userData}><Popularity/></ProtectedRoute>},
    {path:'alphabetical',element:<ProtectedRoute data={userData}><Alphabetical/></ProtectedRoute>},
    {path:'relevance',element:<ProtectedRoute data={userData}><Relevance/></ProtectedRoute>},
    {path:'action',element:  <ProtectedRoute data={userData}><Action/></ProtectedRoute>},
    {path:'action-rpg',element:  <ProtectedRoute data={userData}><ActionRpg/></ProtectedRoute>},
    {path:'battle-royale',element:  <ProtectedRoute data={userData}><BattleRoyale/></ProtectedRoute>},
    {path:'fantasy',element:  <ProtectedRoute data={userData}><Fantasy/></ProtectedRoute>},
    {path:'flight',element:  <ProtectedRoute data={userData}><Flight/></ProtectedRoute>},
    {path:'open-world',element:  <ProtectedRoute data={userData}><OpenWorld/></ProtectedRoute>},
    {path:'racing',element:  <ProtectedRoute data={userData}><Racing/></ProtectedRoute>},
    {path:'shooter',element:  <ProtectedRoute data={userData}><Shooter/></ProtectedRoute>},
    {path:'social',element:  <ProtectedRoute data={userData}><Social/></ProtectedRoute>},
    {path:'sports',element:  <ProtectedRoute data={userData}><Sports/></ProtectedRoute>},
    {path:'zombie',element:  <ProtectedRoute data={userData}><Zombie/></ProtectedRoute>},
    {path:'details/:id',element: <ProtectedRoute data={userData}><Details/></ProtectedRoute>},
    {path:'register',element:<Register/>},
    {path:'login',element: <Login decodedData={decodedData}/>},
  ]}])
  return (<RouterProvider router={roots}/>)
}
