import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination, Navigation,Autoplay } from "swiper";
import 'swiper/css';
import 'swiper/css/autoplay';
import { useParams } from 'react-router-dom';
import CircleLoader from "react-spinners/CircleLoader";

export default function Details() {
  let params = useParams();
  let [data, setdata] = useState([]);
  let [screenshots, setScreenshots] = useState([]);
  let [Loading, setLoading] = useState(true);
  useEffect(() => {
    getData();
    newData()
  }, []);
  
  let newData =async ()=>{
    let data = axios.get('https://www.freetogame.com/api/game?id=452', {
      headers :{
        'X-RapidAPI-Key': 'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
      }
    })
    console.log(data);
  }

  let getData =async()=>
  {
    let data = await axios.get("https://free-to-play-games-database.p.rapidapi.com/api/game", {
      headers :{
        'X-RapidAPI-Key': 'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
      },
      params:{ "id":params.id}
    }).then(data=>{
        setdata(data.data);
        if(data.data.screenshots.length>0)
        {
          setScreenshots(data.data.screenshots)
        }
        setLoading(false)})
  }
return (<>{Loading?<div className='loadingPage'> <CircleLoader
color={"#4e95ff"}
loading={Loading}
size={100}
speedMultiplier= {1.5}
/></div> : <> {data? <div className="container my-5 pt-5">
<div className="row g-3">
  <div className="col-md-4">
    <img src={data.thumbnail} className='w-100' alt="" />
    <div className='row mt-2 ms-2'>
      <span className='col-3 free-game'>FREE</span>
      <a className='col-8 offset-1 playGame' href={data.game_url} target='_blank'><button className='w-100'>PLAY NOW <i _ngcontent-jns-c6="" className="fas fa-sign-out-alt"></i></button></a>
    </div>
  </div>
  <div className="col-lg-8">
    <div className="game-info">
      <h1>{data.title}</h1>
      <h5>About {data.title} </h5>
      <p>{data.description}</p>
      {data.minimum_system_requirements?<div className='requirements'>
      <h5>Minimum System Requirements</h5>
      <p className='p-0 m-0'><span className='fw-bold fs-6'>graphics</span> : {data.minimum_system_requirements.graphics}</p>
      <p className='p-0 m-0'><span className='fw-bold fs-6 '>memory</span> : {data.minimum_system_requirements.memory}</p>
      <p className='p-0 m-0'><span className='fw-bold fs-6'>os</span> : {data.minimum_system_requirements.os}</p>
      <p className='p-0 m-0'><span className='fw-bold fs-6'>processor </span> : {data.minimum_system_requirements.processor }</p>
      <p className='p-0 m-0'><span className='fw-bold fs-6'>storage  </span> : {data.minimum_system_requirements.storage  }</p>
      </div>:''}
      {screenshots?.length>0?<div>
        <h4 className='mt-3'>{data.title} Screenshots</h4>
        <Swiper
          modules={[Pagination,Navigation,Autoplay]}
          autoplay={{delay:1500}}
          speed={500}
          grabCursor={true}
          loop={true}
          >
         
          {screenshots.map((item,index)=> <SwiperSlide key={index}>
              <div className='item'>
                <img src={item.image} height='500' className='' alt=""/>
              </div>
              </SwiperSlide>)} 
        </Swiper>
      </div>:''}
      <div className='my-2'>
        <h4 className='h2'>Additional Information</h4>
         <div className="row">
            <div className="col-md-4 col-6"> 
              <p className='text-muted p-0 m-0'>title</p>
              <p>{data.title}</p>
            </div>
            <div className="col-md-4 col-6"> 
              <p className='text-muted p-0 m-0'>Developer</p>
              <p>{data.developer}</p>
            </div>
            <div className="col-md-4 col-6"> 
              <p className='text-muted p-0 m-0'>Publisher</p>
              <p>{data.publisher}</p>
            </div>
            <div className="col-md-4 col-6"> 
              <p className='text-muted p-0 m-0'>Release Date</p>
              <p>{data.release_date}</p>
            </div>
            <div className="col-md-4 col-6"> 
              <p className='text-muted p-0 m-0'>Genre</p>
              <p>{data.genre}</p>
            </div>
            <div className="col-md-4 col-6"> 
              <p className='text-muted p-0 m-0'>Platform</p>
              <p>{data.platform}</p>
            </div>
         </div>
      </div>     
    </div>
  </div>
</div>
</div> :''}</>}
 
   
    
    
  </>)}
