import { createContext, useEffect, useState } from "react";
import axios from 'axios';

export let CategoryContext = createContext(0);
export default function CategoryProvider(props){
  let [Loading, setLoading] = useState();
    let [Action, setAction] = useState([]);
    let [ActionRpg, setActionRpg] = useState([]);
    let [BattleRoyale, setBattleRoyale] = useState([]);
    let [Fantasy, setFantasy] = useState([]);
    let [Flight, setFlight] = useState([]);
    let [OpenWorld, setOpenWorld] = useState([]);
    let [Racing, setRacing] = useState([]);
    let [Shooter, setShooter] = useState([]);
    let [Social, setSocial] = useState([]);
    let [Sports, setSports] = useState([]);
    let [Zombie, setZombie] = useState([]);

    let getDataByCategory =async(categ,callback)=>
    {
      setLoading(true)
        let {data} = await axios.get("https://free-to-play-games-database.p.rapidapi.com/api/games", {
          headers :{
            'X-RapidAPI-Key': 'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
            'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
          },
          params: { "category":categ}
        });
          callback(data);
      setLoading(false);
    }
      
      useEffect(() => {
        getDataByCategory('action',setAction);
        getDataByCategory('action-rpg',setActionRpg);
        getDataByCategory('battle-royale',setBattleRoyale);
        getDataByCategory('fantasy',setFantasy);
        getDataByCategory('flight',setFlight);
        getDataByCategory('open-world',setOpenWorld);
        getDataByCategory('racing',setRacing);
        getDataByCategory('shooter',setShooter);
        getDataByCategory('social',setSocial);
        getDataByCategory('sports',setSports);
        getDataByCategory('zombie',setZombie);
      }, [])
      

    return <CategoryContext.Provider value={{Loading,Action,ActionRpg,BattleRoyale,Fantasy,Flight,OpenWorld,Racing,Shooter,Social,Sports,Zombie}}>
            {props.children}
    </CategoryContext.Provider>

}