import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';
import CircleLoader from "react-spinners/CircleLoader";


export default function All() {
  let [Loading, setLoading] = useState();
  const [count, setcount] = useState(20)
  const [data,setdata] = useState([]);
  let getData =async()=>
  {
    let {data} = await axios.get("https://free-to-play-games-database.p.rapidapi.com/api/games", {
      headers :{
        'X-RapidAPI-Key': 'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
        'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
      }});
    setcount(count+20);
    setdata(data.slice(0,count))
    setLoading(false);
  }
  useEffect(() => {
    setLoading(true);
    getData();
  }, []); 
  
  return (<> {Loading? <div className='loadingPage'> <CircleLoader
      color={"#4e95ff"}
      loading={Loading}
      size={100}
      speedMultiplier= {1.5}
    /></div> :
      <div className="container pt-5 my-5">
        <div className="row g-4">
          {data.map((item,index)=><div key={index} className='col-xl-3 col-gl-4 col-md-6 item'>
                  <Link className='text-decoration-none' to={`/details/${item.id}`}>
                      <img src={item.thumbnail} className="w-100" alt=""/>
                      <div className='description px-3'>
                        <div className='d-flex justify-content-between align-items-center'>
                          <h2 className='text-muted h4 text-truncate'>{item.title}</h2>
                          <span className={`badge bg-info p-2`}>FREE</span>
                        </div>
                        <p className='text-truncate text-muted'>{item.short_description}</p>
                        <div className='d-flex justify-content-between align-items-center'>
                          <span className='blus badge'>+</span>
                          <div>
                            <p className='badge genre me-2'>{item.genre}</p>
                            {item?.platform==="PC (Windows)"? <i _ngcontent-akf-c7="" className={`fab fa-windows text-info `}></i>:<i className=" fa-solid fa-mobile text-info"></i>}
                          </div>
                        </div>
                      </div>   
                  </Link>
          </div>)}
        </div>
        <div onClick={getData} className="w-100 text-center my-4"> <button className='moreGames-btn'>More Games</button></div>
      </div> } 
    </>)
}
