import axios from 'axios';
import Joi, { string } from 'joi';
import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import style from './Register.module.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { useEffect } from 'react';

export default function Register() {
    let navigate =useNavigate();
    useEffect(() => {
    //animation for header
    AOS.init({});
    }, [])
    
    const [user, setuser] = useState({
            'first_name':'',
            "last_name":"",
            "email":"",
            "password":"",
            "age":""
        }) 

        const [error, seterror] = useState([])
    let getInputValue = (e)=>
    {
        let newUser = {...user}
        newUser[e.target.name]= e.target.value;
        setuser(newUser)
    }
    let submiteFormate = async(e)=>{
        e.preventDefault();
       let valid =  getValid();
       if(valid.error)
       {
            seterror(valid.error.details)
       }
       else{
        let {data} = await axios.post("https://sticky-note-fe.vercel.app/signup",user);
        if(data.message ==="success")
        {
            goToLogin();
        }
        else{alert(data.message)}
       }
        
    }
    let getValid=()=>
    {
        let scehma = Joi.object({
            first_name: Joi.string().alphanum().required().min(3).max(20),
            last_name: Joi.string().alphanum().required().min(3).max(20),
            email:Joi.string().required().email({tlds:{allow:['com','net']}}),
            password:Joi.string().alphanum().required(),
            age:Joi.number().required().min(10).max(80)
        })
        return scehma.validate(user) 
    }
    let goToLogin = ()=>
    {
        navigate('/login')
    }
  return (<>
  <div className="container mt-5 pt-3">
    <div className='row mt-3 gx-3'>
        <div className={`col-md-6`}  data-aos="zoom-in" data-aos-delay={200} data-aos-offset={0}>
           {style.bgImg?<div className={`${style.bgImg}`}></div>:''} 
        </div>
        <div className={`col-md-6 ${style.RegForm} pt-3`}>
            <h4 className='text-muted text-center'  data-aos="fade-down" data-aos-delay={400} data-aos-offset={0}>Create My Account!</h4>
            {}
           {error.map((err,index)=><div className='alert alert-danger' key={index}>{err.message}</div>)}
            <form onSubmit={submiteFormate}>
                <input onChange={getInputValue} type="text" className={`form-control `} placeholder='First Name' name='first_name'  data-aos="fade-down" data-aos-delay={500} data-aos-offset={0}/>
                <input onChange={getInputValue} type="text" className={`form-control`} placeholder='Last Name' name='last_name' data-aos="fade-down" data-aos-delay={600} data-aos-offset={0}/>
                <input onChange={getInputValue} type="email" className='form-control ' placeholder='Email' name='email'  data-aos="fade-down" data-aos-delay={700} data-aos-offset={0}/>
                <input onChange={getInputValue} type="number" className='form-control' placeholder='age' name='age' data-aos="fade-down" data-aos-delay={800} data-aos-offset={0} />
                <input onChange={getInputValue} type="password" className='form-control ' placeholder='Password' name='password' data-aos="fade-down" data-aos-delay={900} data-aos-offset={0}/>
                <button className={`btn form-control ${style.btn}`}  data-aos="fade-down" data-aos-delay={1000} data-aos-offset={0}>Create Account</button>
            </form>
            <p className={`text-center text-muted fs-6 mt-2 ${style.parg}`}>This site is protected by reCAPTCHA and the Google <a href="https://policies.google.com/privacy">Privacy Policy</a> and <a href="https://policies.google.com/terms">Terms of Service</a> apply.</p>
            <hr />
            <p className={`text-center text-muted ${style}`}>Already a member? <Link to={'/login'} className='text-decoration-none'>Log In </Link></p>
        </div>
    </div>
    </div>
    </>)
}


