import React from 'react'
import { Link, NavLink } from 'react-router-dom'
import logo from '../../imgs/logo.png'
import style from './Navbar.module.css'

export default function Navbar({data,logOut}) {
  return (<>
<nav className={`navbar navbar-expand-lg navbar-dark shadow-lg fixed-top ${style.navColor}`}>
  <div className="container d-flex justify-content-between">
    <a className="navbar-brand w-25" href="#">
      <img src={logo} className="w-25" alt="" />
      <span>Game Over</span>
    </a>
    {data?<button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>:''}
    {data? <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className="nav-link" to={''}>Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link" to={'all'}>All</NavLink>
        </li>
        <li className="nav-item dropdown">
          <NavLink className="nav-link dropdown-toggle" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            Platforms
          </NavLink>
          <ul className="dropdown-menu">
            <li><NavLink to={'/pc'} className="dropdown-item" href="#">pc</NavLink></li>
            <li><NavLink to={'/browser'} className="dropdown-item" href="#">browser</NavLink></li>
          </ul>
        </li>
        <li className="nav-item dropdown">
          <NavLink className="nav-link dropdown-toggle" to={'sortby'} role="button" data-bs-toggle="dropdown" aria-expanded="false">
            sort-by
          </NavLink>
          <ul className="dropdown-menu">
            <li><NavLink to={'/release-date'}  className="dropdown-item" href="#">release-date</NavLink></li>
            <li><NavLink to={'/popularity'} className="dropdown-item" href="#">popularity</NavLink></li>
            <li><NavLink to={'/alphabetical'} className="dropdown-item" href="#">alphabetical</NavLink></li>
            <li><NavLink to={'/relevance'} className="dropdown-item" href="#">relevance</NavLink></li>
          </ul>
        </li>
      
        <li className="nav-item dropdown">
          <NavLink className="nav-link dropdown-toggle" to={'categories'} role="button" data-bs-toggle="dropdown" aria-expanded="false">
          Categories
          </NavLink>
          <ul className="dropdown-menu">
            <li><NavLink to={'/shooter'} className="dropdown-item" href="#">shooter</NavLink></li>
            <li><NavLink to={'/racing'} className="dropdown-item" href="#">racing</NavLink></li>
            <li><NavLink to={'/sports'} className="dropdown-item" href="#">sports</NavLink></li>
            <li><NavLink to={'/social'} className="dropdown-item" href="#">social</NavLink></li>
            <li><NavLink to={'/open-world'} className="dropdown-item" href="#">open-world</NavLink></li>
            <li><NavLink to={'/zombie'} className="dropdown-item" href="#">zombie</NavLink></li>
            <li><NavLink to={'/fantasy'} className="dropdown-item" href="#">fantasy</NavLink></li>
            <li><NavLink to={'/action-rpg'} className="dropdown-item" href="#">action-rpg</NavLink></li>
            <li><NavLink to={'/action'} className="dropdown-item" href="#">action</NavLink></li>
            <li><NavLink to={'/flight'} className="dropdown-item" href="#">flight</NavLink></li>
            <li><NavLink to={'/battle-royale'} className="dropdown-item" href="#">battle-royale</NavLink></li>
          </ul>
        </li>
      
      </ul>
    </div>:''}
   {data? <div>
          <button onClick={logOut} className={`${style.logbtn} btn`}>Log Out</button>
    </div>:<div className={`d-flex justify-content-between ${style.buttons}`}>
            <div className=''>
              <NavLink to={'login'} className={`${style.loginBtn} btn`}>login</NavLink>
            </div>
            <div>
              <NavLink to={'register'} className={`${style.logbtn} btn`}>join free</NavLink>
            </div>
                </div>}
  </div>
</nav>

    </>)
}
