import { createContext, useEffect, useState } from "react";
import axios from 'axios';

export let Context = createContext(0);
export default function ContextProvider(props){
    let [Loading, setLoading] = useState();
    const [pc, setPc] = useState([]);
    const [browser, setBrowser] = useState([]);
    const [relevance, setRelevance] = useState([]);
    const [alphabetical, setAlphabetical] = useState([]);
    const [popularity, setPopularity] = useState([]);
    const [releaseDate, setreleaseDate] = useState([]);

    let getDataByPlatform =async(plat,callback)=>
    {
      setLoading(true)
      let {data} = await axios.get("https://free-to-play-games-database.p.rapidapi.com/api/games", {
        headers :{
          'X-RapidAPI-Key': 'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
          'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
        },
        params: { "platform":plat}
      });
        callback(data);
        setLoading(false);
    }
    let getDataBySort =async(SortBy,callback)=>
    {
      setLoading(true)
      let {data} = await axios.get("https://free-to-play-games-database.p.rapidapi.com/api/games", {
        headers :{
          'X-RapidAPI-Key': 'b52128808dmsh5826403ec30ac21p1b9548jsnfca5769e0b68',
          'X-RapidAPI-Host': 'free-to-play-games-database.p.rapidapi.com'
        },
        params: { "sort-by":SortBy}
      });
        callback(data);
        setLoading(false);
    }
    useEffect(() => {
      getDataByPlatform("pc",setPc);
      getDataByPlatform("browser",setBrowser);
      getDataBySort("relevance",setRelevance);
      getDataBySort("alphabetical",setAlphabetical);
      getDataBySort("popularity",setPopularity);
      getDataBySort("release-date",setreleaseDate);
    }, [])
    
    return <Context.Provider value={{Loading,pc,browser,relevance,alphabetical,popularity,releaseDate}}>
                {props.children}  
            </Context.Provider>
}
