import axios from 'axios';
import React, { useState } from 'react'
import logo from '../../imgs/logo.png'
import { Link, useNavigate } from 'react-router-dom';
import style from './Login.module.css';
import AOS from 'aos';
import 'aos/dist/aos.css';
import { useEffect } from 'react';


export default function Login({decodedData}) {
  let navigate =useNavigate();
  let [Loading, setLoading] = useState(false)
  const [error, seterror] = useState('');

  useEffect(() => {
     //animation for header
      AOS.init({});
  }, [Loading]);
  const [user, setuser] = useState({
          "email":"",
          "password":""
      });
      let getInputValue = (e)=>
      {
          let newUser = {...user}
          newUser[e.target.name]= e.target.value;
          setuser(newUser)
      }
      let submiteFormate = async(e)=>
      {
        e.preventDefault();
        setLoading(true);
          let {data} = await axios.post("https://sticky-note-fe.vercel.app/signin",user);
          if(data.message ==="success")
          {
            localStorage.setItem('token',data.token);
            goToHome();
            decodedData();
            setLoading(false);
          }
          else{seterror(data.message)
            setLoading(false);}
         
      }
      let goToHome = ()=>
      {
          navigate('/')
      }

  return (
    <>
    <div className="container pt-5 mt-4">
    <div className={`row mt-3 gx-5`}>
        <div className={`col-md-6 d-none d-md-block`} data-aos="fade-right" data-aos-duration={1000} data-aos-delay={200} data-aos-offset={0}>
        <div  className={`${style.bgImg}`}></div>
        </div>
        <div className={`col-md-6 pt-3 ${style.RegForm}`} data-aos="fade-left" data-aos-delay={200} data-aos-duration={1000} data-aos-offset={0}>
            <div className='w-25 m-auto'><img src={logo} className="w-100" alt="" /></div>
            <h4 className='text-muted text-center my-3 '>Log in to GameOver</h4>
            {}
            {error?<div className='alert py-2 w-75 m-auto alert-danger'>{error}</div>:""}
            <form onSubmit={submiteFormate} className="w-75 m-auto">
                <input onChange={getInputValue} type="email" className='form-control my-3 ' placeholder='Email' name='email' />
                <input onChange={getInputValue} type="password" className='form-control my-3 ' placeholder='Password' name='password' />
            
              {Loading===true?<button className={`btn form-control my-3 ${style.btn}`}>...Loading</button>:<button className={`btn form-control my-3 ${style.btn}`}>Login</button>}
            </form>
            <hr />
            <div className='text-center m-auto'>
            <Link to={'/login'} className='text-decoration-none' >Forgot Password?</Link>
            <p>Not a member yet? <Link to={'/Register'} className='text-decoration-none'> Create Account </Link></p>
            </div>
        </div>
    </div>
    </div>
    </>
  )
}
